@import url(https://fonts.googleapis.com/css?family=Bungee);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  color: white;
}

.doggo-image-container {
  border-color: white;
  border-style: solid;
  border-width: 2px;
  border-radius: 2px;
  width: 300px;
  height: 300px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  margin-bottom: 20px;
}

.doggo-image {
  object-fit: cover;
  width: 300px;
  height: 300px;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
}

.doggo-images {
  max-width: 100%;

}

video.doggo-image {
  z-index: 1;
}

img.doggo-image {
  z-index: 0;
}

.App {
  /* text-align: center; */
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.hidden {
  display: none;
}

.input-container {
  position: absolute;
  bottom: 30px;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  width: 100%;
  max-width: 500px;
}

.input-button {
  cursor: pointer;
  color: white;
  font-size: 16px;
  background-color:#4CAF50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  margin: 10px;
  box-shadow: 0 7px #999;
  font-family: 'Bungee', cursive;
  text-decoration: none;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.input-button:hover {
  background-color: #3E8E41;
}

.input-button:active {
  background-color: #3E8E41;
  box-shadow: 0 5px #666;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

#file-input + label {
  cursor: pointer;
  color: white;
  font-size: 16px;
  background-color:#4CAF50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  margin: 10px;
  box-shadow: 0 7px #999;
  font-family: 'Bungee', cursive;
  text-decoration: none;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);;
}

#file-input + label:hover {
  background-color: #3E8E41;
}

#file-input + label:active {
  background-color: #3E8E41;
  box-shadow: 0 5px #666;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

button[disabled=disabled], button[disabled] {
  background-color: grey;
}

button[disabled=disabled]:hover, button[disabled]:hover {
  background-color: grey;
}

button[disabled=disabled]:active, button[disabled]:active {
  box-shadow: 0 7px #999;
  -webkit-transform: none;
          transform: none;
}

input[type=file] {
  width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.column {
  margin-top: 20px;
  margin-bottom: 20px;
}

.card {
  background-color: whitesmoke;
  box-shadow: 0px 2px 5px 2px rgba(0, 0, 0, 0.1);
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 20px;
  padding-left: 20px;
  border-radius: 2px;
  max-width: 320px;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
}

table {
  width: 100%;
}

.pred-object {
  text-align: left;
}

.pred-percent {
  text-align: center;
}

div > .info-button {
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.info-button {
  color: white;
  border-radius: 50%;
  border-style: solid;
  border-width: 2px;
  background: none;
  padding: 0px;
  text-align: center;
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.info-text {
  color: #282c34;
}
